import React, { Component } from "react";
import Tag from "./tag";
import Toolbar from "./toolbar";
import TargetCard from "./targetCard";

const requireContext = require.context("../images", true, /^\.\/.*\.png$/);
const images = requireContext.keys().map(requireContext);
let iconImages = {};
requireContext.keys().map((req, index) => {
  let iconName = req.split("./")[1];
  iconName = iconName.split(".")[0];
  const iconImagesObj = {};
  iconImagesObj[iconName] = images[index];
  iconImages = {
    ...iconImages,
    ...iconImagesObj
  };
  return {};
});
console.log(iconImages.topic);

class NewHabit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      marginLeft: "0%",
      move: [
        { title: "跑步", icon: iconImages.ic_run },
        { title: "篮球", icon: iconImages.ic_basketball },
        { title: "健身操", icon: iconImages.ic_calisthenics },
        { title: "瑜伽", icon: iconImages.ic_yoga },
        { title: "跳舞", icon: iconImages.ic_dance },
        { title: "羽毛球", icon: iconImages.ic_badminton },
        { title: "滑板", icon: iconImages.ic_skateboard }
      ],
      study: [
        { title: "阅读", icon: iconImages.ic_read },
        { title: "练字", icon: iconImages.ic_calligraphy },
        { title: "复习/考试", icon: iconImages.ic_review },
        { title: "写作", icon: iconImages.ic_writing },
        { title: "绘画", icon: iconImages.ic_painting },
        { title: "学吉他", icon: iconImages.ic_guitar },
        { title: "线上课", icon: iconImages.ic_online_courses },
        { title: "摄影", icon: iconImages.ic_photography }
      ],
      health: [
        { title: "早睡", icon: iconImages.ic_sleep },
        { title: "喝水", icon: iconImages.ic_drink_water },
        { title: "早起", icon: iconImages.ic_get_up },
        { title: "吃早餐", icon: iconImages.ic_breakfast },
        { title: "刷牙", icon: iconImages.ic_brush_teeth },
        { title: "护眼", icon: iconImages.ic_eyecare }
      ],
      life: [
        { title: "攒钱", icon: iconImages.ic_save_money },
        { title: "情绪管理", icon: iconImages.ic_emotion_management },
        { title: "护肤", icon: iconImages.ic_skin_care },
        { title: "记手账", icon: iconImages.ic_hand_account },
        { title: "一日三餐", icon: iconImages.ic_meal },
        { title: "树洞", icon: iconImages.ic_tree_hole },
        { title: "日记", icon: iconImages.ic_diary },
        { title: "每日收纳", icon: iconImages.ic_take_in },
        { title: "电影", icon: iconImages.ic_movie }
      ]
    };
    this.changeMarginLeft = this.changeMarginLeft.bind(this);
  }
  state = {};
  changeMarginLeft(value) {
    this.setState({
      marginLeft: `${value === 0 ? "0%" : `-${value * 100}%`}`
    });
  }
  render() {
    return (
      <div className="new-habit">
        <Toolbar />
        <Tag changeMarginLeft={this.changeMarginLeft} />
        <div className="tabs">
          <div
            className="tabs-content tabs-content-animated"
            style={{ marginLeft: this.state.marginLeft }}
          >
            <div className="tabpane tabpane-active">
              <TargetCard cardInfo={this.state.move} />
            </div>
            <div className="tabpane tabpane-inactive">
              <TargetCard cardInfo={this.state.study} />
            </div>
            <div className="tabpane tabpane-inactive">
              <TargetCard cardInfo={this.state.health} />
            </div>
            <div className="tabpane tabpane-inactive">
              <TargetCard cardInfo={this.state.life} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default NewHabit;
