import React, { Component } from "react";

class Mask extends Component {
  constructor(props) {
    super(props);
    this.state = {
      style: {
        left: -280,
        transition: "left 0.2s ease 0s"
      },
      maskStyle: { display: "none", transition: "display 1s ease 0s" }
    };
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick = () => {
    this.props.changeStyle(this.state.style, this.state.maskStyle);
  };
  state = {};
  render() {
    return (
      <div
        className="mask"
        style={{ display: this.props.maskStyle.display, transition: "" }}
        onClick={this.handleClick}
      />
    );
  }
}

export default Mask;
