import React, { Component } from "react";

const tagName = ["运动", "学习", "健康", "生活"];
class Tag extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: 0
    };
  }
  handleClick = index => {
    this.setState({
      selected: index
    });
    this.props.changeMarginLeft(index);
  };
  state = {};
  render() {
    let self = this;
    return (
      <div className="tag">
        {tagName.map((tag, index) => {
          return (
            <div
              key={index}
              className={[
                "tag-item",
                index === this.state.selected ? "tag-active" : null
              ].join(" ")}
              //onClick={() => this.handleClick(index)}
              onClick={self.handleClick.bind(self, index)}
            >
              {tag}
            </div>
          );
        })}
      </div>
    );
  }
}

export default Tag;
