import React, { Component } from "react";
import habits from "../images/habits_selected.png";
import goals from "../images/goals.png";
import topic from "../images/topic.png";
import more from "../images/more.png";

const menus = [
  { icon: habits, title: "习惯" },
  { icon: goals, title: "目标" },
  { icon: topic, title: "公共日志" },
  { icon: more, title: "个人中心" }
];

class BottomNavigation extends Component {
  state = {};
  render() {
    return (
      <div className="bottom-menu">
        <div className="bottom-menu-wrapper">
          {menus.map((menu, index) => {
            return (
              <a key={index} className="item" href="#/user">
                <img className="icon" src={menu.icon} alt="" />
                <span className="title">{menu.title}</span>
              </a>
            );
          })}
        </div>
      </div>
    );
  }
}

export default BottomNavigation;
