import React, { Component } from "react";
import BottomNavigation from "./bottomNavigation";
import Toolbar from "./toolbar";
import DrawerLayout from "./drawerLayout";
import Mask from "./mask";
import ViewPager from "./viewPager";

class Habits extends Component {
  constructor(props) {
    super(props);
    this.state = {
      style: { left: -280, transition: "" },
      maskStyle: { display: "none", transition: "" }
    };

    this.changeStyle = this.changeStyle.bind(this);
  }
  state = {};
  changeStyle(style, maskStyle) {
    this.setState({
      style,
      maskStyle
    });
  }
  render() {
    return (
      <div>
        <Mask changeStyle={this.changeStyle} maskStyle={this.state.maskStyle} />
        <Toolbar changeStyle={this.changeStyle} />
        <DrawerLayout style={this.state.style} />
        <ViewPager />
        <BottomNavigation />
      </div>
    );
  }
}

export default Habits;
