import React, { Component } from "react";

class DrawerLayout extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  state = {};
  render() {
    return (
      <div
        className="drawer-layout"
        style={{
          left: this.props.style.left,
          transition: this.props.style.transition
        }}
      >
        Hello
      </div>
    );
  }
}

export default DrawerLayout;
