import React, { Component } from "react";

class Toolbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      style: {
        left: 0,
        transition: "left 0.5s ease 0s"
      },
      maskStyle: { display: "block", transition: "opacity 0.5s ease 0s" }
    };
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick() {
    this.props.changeStyle(this.state.style, this.state.maskStyle);
  }
  state = {};
  render() {
    return (
      <div className="toolbar">
        <div className="navigation-button" onClick={this.handleClick}>
          <img src={require("../images/ic_left_dragview.png")} alt="" />
        </div>
        <div className="title">打卡</div>
        <div className="action-menu">
          <a className="action-add" href="#/new">
            <img
              src={require("../images/ic_add_or_manage_target.png")}
              alt=""
            />
          </a>
          <img src={require("../images/ic_share_title.png")} alt="" />
        </div>
      </div>
    );
  }
}

export default Toolbar;
