class TargetCard extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  state = {};
  render() {
    return (
      <div className="card">
        {this.props.cardInfo.map((info, index) => {
          return (
            <div key={index} className="card-item">
              <div className="card-info">
                <div className="card-name">{info.title}</div>
                <div className="card-count">***人坚持</div>
              </div>
              <div className="card-icon">
                <img src={info.icon} alt="" />
              </div>
            </div>
          );
        })}
        {/* {Object.values(iconImages).map((icon, index) => {
          return (
            <div key={index} className="card-item">
              <div className="card-info">
                <div className="card-name">篮球</div>
                <div className="card-count">***人坚持</div>
              </div>
              <div className="card-icon">
                <img src={icon} alt="" />
              </div>
            </div>
          );
        })} */}
      </div>
    );
  }
}

export default TargetCard;
